<template>
  <div v-if="details_info[0].details_name">
          <nave-bar :showIcon="true" :title=title />
    <!--  -->
    <div class="swipe">
    <van-swipe class="my-swipe" :autoplay="2400" loop touchable  indicator-color="white">
      <van-swipe-item v-for="(item, i) in details_info[0].pics" :key="i">
        <a>
          <!--  -->
           <!-- :href=item.navigator_url -->
          <img :src="item.pics_big">
        </a>
      </van-swipe-item>
    </van-swipe>
    </div>
    <!--  -->
<div class="details-info-box">
  <!--  -->
  <div class="price">{{details_info[0].details_price}}</div>
  <!--  -->
  <div class="details-info-body">
    <!--  -->
    <div class="details-name">{{details_info[0].details_name}}</div>
    <!--  -->
    <div class="shou">
      <van-icon name="star-o" />
      <span></span>
    </div>
  </div>
  <!--  -->
  <div class="yun"></div>
</div>

<!--  -->
<!--  -->
<van-goods-action>
  <van-goods-action-icon icon="chat-o" text="" dot />
  <van-goods-action-icon @click="gotoCart" :badge=badge icon="cart-o" text="" />
  <van-goods-action-icon icon="shop-o" text="" badge="12" />
  <van-goods-action-button @click="add_Cart" type="warning" text="" />
  <van-goods-action-button type="danger" text="" />
</van-goods-action>

    </div>
</template>

<script>
import { Details } from '../../utils/Sql/details'
import { mapState, mapMutations, mapGetters } from 'vuex'
export default {
  data () {
    return {
      // 
      details_info: {},
      badge: this.total,
      title: ''
    }
  },
  computed: {
    ...mapState('Cart', ['cart']),
    ...mapGetters('Cart', ['total'])
  },
  watch: {
    total: {
      handler (newVal) {
        this.badge = newVal
      },
      immediate: true
    }
  },
  created () {
    this.getDetails(this.$route.query.details_id)
    console.log(this.details_info[0])
  },
  methods: {
    ...mapMutations('Cart', ['addCart']),
    // 
    getDetails (id) {
      const res = Details.data
      const data = res.filter((item) => {
        if (parseInt(id) === parseInt(item.details_id)) return item
      })
      this.details_info = data
    },
    // 
    gotoCart () {
      this.$router.push({
        path: '/cart'
      })
    },
    // 
    add_Cart () {
      // 2. 
      const data = {
        details_id: this.details_info[0].details_id, // Id
        details_name: this.details_info[0].details_name, // 
        details_price: this.details_info[0].details_price, // 
        details_count: 1, // 
        details_small_logo: this.details_info[0].details_small_logo, // 
        details_state: true // 
      }
      this.addCart(data)
    }
  }
}
</script>

<style lang="scss" scoped>
/*  */
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 16px;
    text-align: center;
  }
  .swipe img{
    display: inline-block;
    width: 100%;
    height: 350px;
}
// 
.details-info-box {
  padding: 10px;
  padding-right: 0;
  .price {
    color: #c00000;
    font-size: 18px;
    margin: 10px 0;
  }
  .details-info-body {
    display: flex;
    justify-content: space-between;
    .details-name {
      font-size: 13px;
      padding-right: 10px;
    }
    // 
    .shou {
      width: 120px;
      font-size: 16px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-left: 1px solid #efefef;
      color: gray;
      span{
        font-size: 14px;
      }
    }
  }
  // 
  .yun {
    margin: 10px 0;
    font-size: 12px;
    color: gray;
  }
}
</style>
