<template>
  <div>

    <!--  -->
    <div class="swipe">
    <van-swipe class="my-swipe" :autoplay="2400" loop touchable  indicator-color="white">
      <van-swipe-item v-for="(item, index) in swipers" :key="index" @click="goDetails()">
        <a>
          <!--  -->
           <!-- :href=item.navigator_url -->
          <img :src=item.image_src>
        </a>
      </van-swipe-item>
    </van-swipe>
    </div>

    <!--  -->
    <div class="navigator">
      <div class="navigator-flex" v-for="(item, index) in navigator" :key="index">
        <a :href=item.navigator_url>
          <!--  -->
           <!-- :href=item.navigator_url -->
          <img :src=item.image_src>
        </a>
      </div>
    </div>

    <!--  -->
    <div>
      <!-- item -->
      <div v-for="(item, index) in floors" :key="index">
        <!--  -->
        <img :src=item.floor_title.image_src class="floor-title">
        <!--  -->
        <div class="floor-img">
          <!--  -->
          <div class="floor-left">
            <img :src=item.floor_left.image_src :style="{width: item.product_list[0].image_width/2 + 'px'}">
          </div>
          <!--  -->
          <div class="floor-img-right">
            <!--  -->
            <div v-for="(item2, index2) in item.product_list" :key="index2" @click="goDetails()">
              <img :src=item2.image_src :style="{width: item2.image_width/2 + 'px'}">
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
// 
import { Swipers, Navigator, Floors } from '@/utils/Sql/index.js'
export default {
  data () {
    return {
      // 
      swipers: [],
      // 
      navigator: [],
      // 
      floors: []
    }
  },
  created () {
    this.swipers = Swipers.data
    this.navigator = Navigator.data
    // 
    this.getFloors()
  },
  methods: {
    getFloors () {
      const { data: res } = Floors
      this.floors = res
    },
    goDetails () {
      this.$router.push({
        path: '/detailslist'
      })
    }
  }
}
</script>

