    line-height: 16px;
    text-align: center;
  }
  .swipe img{
    display: inline-block;
    width: 100%;
    height: 150px;
}
.navigator{
  display: flex;
  justify-content: space-around;
  margin: 16px 0;
  img {
    width: 93.75px;
    height: 100px;
  }
}
// 
.floor-title {
  height: 60px;
  width: 100%;
  display: flex;
  margin: 8px 0;
}
.floor-img{
  display: flex;
  padding-left: 10px;
  .floor-left {
    img{
      height: 100%;
    }
  }
}
.floor-img-right{
display: flex;
flex-wrap: wrap;
justify-content: space-around;
    img{
