<template>
<div>
<van-nav-bar
  title=""
  left-text=""
  right-text=""
  @click-left="onClickLeft"
  @click-right="onClickRight"
/>
<van-form @submit="onSubmit" submit-on-enter class="form">
  <van-field
   class="buton"
    v-model="register.username"
    name="username"
    label=""
    placeholder=""
    :rules="[{ required: true, message: '' }]"
  />
  <van-field
  class="buton"
    v-model="register.password"
    :type=type
    name="password"
    label="    "
    placeholder=""
    :rules="[{ pattern, required: true, message: '' }]"
  />
  <van-field
  class="buton"
    v-model="register.passwordagin"
    :type=type
    name="password"
    label=""
    placeholder=""
    :rules="[{ validator, required: true, message: '' }]"
  />
  <div style="margin: 16px;">
    <van-button round block type="info" native-type="submit"></van-button>
  </div>
</van-form>
<van-divider content-position="left"><van-icon @click="change(1)" name="eye-o" /></van-divider>
<div class="tishi">
  <h5></h5>
  <p>6</p>
  <p>6</p>
  <p></p>
  <p></p>
  <p>~~~</p>
</div>
</div>
</template>

<script>
import { Toast } from 'vant'
import { mapMutations, mapState } from 'vuex'
export default {
  data () {
    return {
      title: '',
      pattern: /^\d{6}$/,
      type: 'password',
      istype: true,
      register: {
        username: '',
        password: '',
        passwordagin: ''
      }
    }
  },
  created () {
  },
  computed: {
    ...mapState('User', ['userInfo'])
  },
  methods: {
    ...mapMutations('User', ['Register']),
    //  true false 
    validator (val) {
      return this.register.password === this.register.passwordagin
    },
    onSubmit (data) {
      if (this.userInfo.data.username) {
        this.Register({ data, token: {} })
        Toast.success('')
      } else {
        this.Register({ data, token: {} })
        Toast.success('')
      }
    },
    // 
    onClickLeft () {
      this.register = {
        username: '',
        password: '',
        passwordagin: ''
      }
    },
    change () {
      if (this.istype) {
        this.type = 'text'
        this.istype = false
      } else {
        this.type = 'password'
        this.istype = true
      }
    },
    onClickRight () {
      this.$router.push({
        path: '/login'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.head{
    height: 100px;
    width: 100%;
    text-align: center;
    line-height: 100px;
}
.form {
    .button {
        height:36px ;
    }
}
  .tips-text {
    font-size: 12px;
    color: gray;
    text-align: center;
    line-height: 36px;
    height: 36px;
    width: 100%;
    margin-top: 36px;
  }
  .tishi{
    padding: 0 24px;
    p{
      font-size: 16px;
      color: gray;
    }
  }
</style>
