<template>
  <div>
    <router-view />
    <van-tabbar class="footer" v-model="active" fixed z-index=3 route @change="onChange" placeholder>
      <van-tabbar-item icon="home-o" to="/index"></van-tabbar-item>
      <van-tabbar-item icon="apps-o" dot to="/list"></van-tabbar-item>
      <van-tabbar-item icon="cart-o" :badge=badge to="/cart"></van-tabbar-item>
      <van-tabbar-item icon="contact" dot to="/my"></van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Index',
  data () {
    return {
      // 
      active: 0,
      // 
      badge: this.total
    }
  },
  computed: {
    ...mapGetters('Cart', ['total'])
  },
  watch: {
    total: {
      handler (newVal) {
        this.badge = newVal
      },
      immediate: true
    }
  },
  methods: {
    // 
    onChange (item) {
      this.active = item
    }
  }
}
</script>

<style lang="scss" scoped>
.footer{
  position: fixed;
  bottom: 0;
}
</style>
