<template>
  <div>

<div v-if="detailslist.length !== 0">
      <!--  -->
<div class="cart-title">
  <!--  -->
  <van-icon name="shopping-cart-o" />
  <!--  -->
  <span class="cart-title-text"></span>
</div>

<!--  -->
    <div class="details-list">
      <!-- item-- -->
      <div v-for="(item, index) in detailslist" :key="index">
        <my-list :detailslist="item" :showRadio="true" @chan="change" @chan2="change2" @chan3="change3" />
      </div>
    </div>

<!-- <my-settle /> -->
<div class="footer">
  <van-checkbox  checked-color="#C00000" v-model="isFullCheck" @click="allchecked"></van-checkbox>
  <span>: <span>{{checkedTotal}}</span></span>
  <van-button class="button" type="danger" @click="jie">({{checkedCount}})</van-button>
</div>
</div>

    <!--  -->
    <div class="empty-cart" v-else>
      <img src="@/assets/cart.png" class="empty-img" />
      <span class="tip-text">~</span>
    </div>

  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import { Toast } from 'vant'
export default {
  data () {
    return {
      detailslist: this.cart
    }
  },
  computed: {
    ...mapState('Cart', ['cart']),
    ...mapState('User', ['userInfo']),
    ...mapGetters('Cart', ['total', 'checkedCount', 'checkedTotal']),
    // 
    isFullCheck: {
      get () {
        return this.total === this.checkedCount
      },
      set () {
        return this.total === this.checkedCount
      }
    }
  },
  created () {
    this.getCart()
    console.log(this.detailslist)
  },
  methods: {
    ...mapMutations('Cart', ['updataSave', 'updateAllsave', 'updateCount', 'removeCart']),
    // 
    getCart () {
      this.detailslist = this.cart
    },
    // 
    change (e) {
      this.updataSave(e)
    },
    // 
    allchecked () {
      this.updateAllsave(!this.isFullCheck)
    },
    // 
    change2 (e) {
      this.updateCount(e)
    },
    // 
    change3 (id) {
      this.removeCart(id)
      this.getCart()
      Toast('  !')
    },
    // 
    onClickEditAddress () {
      console.log('')
    },
    // 
    jie () {
      if (this.userInfo.data.username) {
        Toast.loading('')
      } else {
        Toast.fail('')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.cart-title {
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding-left: 5px;
  border-bottom: 1px solid #efefef;

  .cart-title-text {
    margin-left: 10px;
  }
}
.van-submit-bar__bar{
  height: 70px;
}
.footer {
  /*  */
  position: fixed;
  bottom: 70px;
  /*  */
  width: 100%;
  height: 50px;
  font-size: 18px;
  display: flex;
  justify-content: space-around;
  align-content: center;
  span{
    width: 140px;
    line-height: 50px;
    span{
        color: #c00000;
    }
  }
  .button{
    width: 120px;
    background-color: rgb(192,0,0);
  }
}
// 
  .empty-cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 150px;

    .empty-img {
      width: 90px;
      height: 90px;
    }
  }
</style>
